<template>
  <div>
    <el-dialog :visible.sync="visible" :before-close="beforeClose" :append-to-body="true" title="新建地点" width="800px">
      <div class="p15">
        <el-form :show-message=true :inline-message=true ref="form" :model="form" :rules="rules" class="form-text f13 "
                 label-width="108px" size="mini">
          <el-row>
            <el-col :span="22">
              <el-form-item label="地点名：" prop="title">
                <el-input
                  v-model='form.title'>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt5">
            <el-col :span="22">
              <el-form-item label="省 市：" prop="place">
                <el-cascader
                  :options="placeOptions"
                  @active-item-change="handlePlaceChange"
                  :props="placeProps"
                ></el-cascader>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt5">
            <el-col :span="22">
              <el-form-item label="详细地址：" prop="content">
                <el-input
                  v-model='form.detail'>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt5">
            <el-col :span="22">
              <el-form-item label="备注：" prop="content">
                <el-input type="textarea" :rows="4" v-model='form.content'>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer mt10" style="text-align: center">
        <el-button type="primary" @click="closeDialog('save')">保 存</el-button>
        <el-button @click="closeDialog('close')">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    name: 'Dialog',
    props: ['dialogFormVisible'],
    computed: {
      visible: {
        get: function () {
          return this.dialogFormVisible
        },
        set: function () {
        }
      }
    },
    data() {
      return {
        showModal: false,
        placeOptions: [{
          label: '江苏',
          cities: []
        }, {
          label: '浙江',
          cities: []
        }],
        placeProps: {
          value: 'label',
          children: 'cities'
        },
        form: {
          title: '',
          content: '',
          detail: '',
          place: ''
        },
        rules: {
          title: [
            {required: true}
          ]
        }
      }
    },
    methods: {
      closeDialog(method) {
        this.$emit('closeDialog', this.form, method)
        if (method === 'save') {
          this.form = {
            title: '',
            content: '',
            detail: '',
            place: ''
          }
          this.$refs['form'].resetFields()
        }
      },
      handlePlaceChange(val) {
        console.log('active item:', val);
        setTimeout(() => {
          if (val.indexOf('江苏') > -1 && !this.placeOptions[0].cities.length) {
            this.placeOptions[0].cities = [{
              label: '南京'
            }];
          } else if (val.indexOf('浙江') > -1 && !this.placeOptions[1].cities.length) {
            this.placeOptions[1].cities = [{
              label: '杭州'
            }];
          }
        }, 300);
      },
      beforeClose(done) {
        done()
        this.$emit('closeDialog', {}, 'close')
      }
    }
  }
</script>
<style lang="scss">
  .el-input__suffix {
    top: -4px;
  }
  .el-cascader__label {
    top: -4px;
  }
</style>


